import { Text, Pressable } from 'react-native-web';

import * as ACTIONS from '../constants/actions';

import Container from '../components/Container';
import Header from '../components/Header'
import BackButton from '../components/BackButton';
import IntakeContainer from '../components/IntakeContainer';

export default function TherapyBeforeScreen({ dispatch }) {
  return (
    <Container>
      <Header />
      <BackButton dispatch={dispatch} prevScreen='Name' />
      <IntakeContainer>
        <Text
          style={{
            textAlign: 'center',
            padding: 30
          }}
        >Have you had therapy before?</Text>
        <Pressable
          style={{
            width: 250,
            borderColor: '#EBEBEB',
            borderWidth: 1,
            borderRadius: 20,
            shadowColor: '#EAEAEA',
            shadowOffset: {width: 0, height: 8},
            shadowOpacity: .5,
            shadowRadius: 14,
            margin: 20
          }}
          onPress={() => {
            dispatch({
              type: ACTIONS.SET_USER_THERAPYBEFORE,
              payload: true
            })
            dispatch({
              type: ACTIONS.CHANGE_SCREEN,
              payload: {screen: 'TherapyPrefs1'}
            })
          }}
        >
          <Text style={{
            // fontSize: 16,
            color: '#696969',
            textAlign: 'center',
            padding: 10,
          }}>Yes</Text>
        </Pressable>
        <Pressable
          style={{
            width: 250,
            borderColor: '#EBEBEB',
            borderWidth: 1,
            borderRadius: 20,
            shadowColor: '#EAEAEA',
            shadowOffset: {width: 0, height: 8},
            shadowOpacity: .5,
            shadowRadius: 14,
          }}
          onPress={() => {
            dispatch({
              type: ACTIONS.SET_USER_THERAPYBEFORE,
              payload: false
            })
            dispatch({
              type: ACTIONS.CHANGE_SCREEN,
              payload: {screen: 'TherapyPrefs1'}
            })
          }}
        >
          <Text style={{
            // fontSize: 16,
            color: '#696969',
            textAlign: 'center',
            padding: 10,
          }}>No</Text>
        </Pressable>
        </IntakeContainer>
    </Container>
  )
}