import { Text, Pressable, TextInput } from 'react-native-web';

import * as ACTIONS from '../constants/actions';

import Container from '../components/Container';
import Header from '../components/Header'
import IntakeContainer from '../components/IntakeContainer';

export default function UserNameScreen({ dispatch }) {
  let userName = '';
  return (
    <Container>
      <Header />
      <IntakeContainer>
        <Text
          style={{
            textAlign: 'center',
            padding: 30
          }}
        >What is your name?</Text>
        <TextInput
            placeholder="First name"
            placeholderTextColor="#C5C5C6"
            autoComplete="autocomplete"
            // autoCorrect="on"
            autoCapitalize="sentences"
            onChangeText={(text) => userName=text}
            onSubmitEditing={(e) => {
              const value = e.target.value;

              dispatch({
                  type: ACTIONS.SET_USER_NAME,
                  payload: { name: value }
                })
              dispatch({
                type: ACTIONS.CHANGE_SCREEN,
                payload: {screen: 'TherapyBefore'}
              })

            }}
            style={{
              // flex: 1,
              // input
              borderColor: '#BFBFBF',
              borderWidth: 1,
              paddingTop: 4,
              paddingBottom: 5,
              paddingHorizontal: 12,
              borderRadius: 15,

              // text
              fontSize: 16,
            }}
          />
        <Pressable
          style={{
            width: 150,
            borderColor: '#EBEBEB',
            borderWidth: 1,
            borderRadius: 20,
            margin: 50,
            shadowColor: '#EAEAEA',
            shadowOffset: {width: 0, height: 8},
            shadowOpacity: .5,
            shadowRadius: 14,
          }}
          onPress={() => {
            dispatch({
              type: ACTIONS.SET_USER_NAME,
              payload: { name: userName }
            })
            dispatch({
              type: ACTIONS.CHANGE_SCREEN,
              payload: {screen: 'TherapyBefore'}
            })
          }}
        >
          <Text style={{
            // fontSize: 16,
            color: '#696969',
            textAlign: 'center',
            padding: 10,
          }}>Next</Text>
        </Pressable>
        </IntakeContainer>
    </Container>
  )
}