import { useEffect, useRef } from 'react';
import {  usePrevious } from 'react-use';
import {
  fetchGPTAnswer,
  fetchContentModeration,
  buildMessage,
  getLastMessage,
  getPreviousSession,
  getCurrentSession,
  createNextMessagePrompt,
  createSessionNotesPrompt,
  isValidApiKey,
  setAPIKeyFromParms
} from '../utils';

import * as ACTIONS from '../constants/actions';

import Container from '../components/Container';
import Menu from '../components/Menu'
import Header from '../components/Header'
import Chat from '../components/Chat'
import PopUp from '../components/PopUp'
import Footer from '../components/Footer'

export default function ChatScreen({ state, dispatch }) {
  const prevMessages = usePrevious(getCurrentSession(state).messages) || [];
  const scrollViewRef = useRef();

  // // auto scroll to bottom
  useEffect(() => {
    if (getCurrentSession(state).messages.length !== prevMessages.length) {
      // Scroll messages to bottom
      scrollViewRef.current.scrollToEnd({ animated: false });
    }
  }, [state, dispatch, prevMessages])

  // // fill in previous session notes if empty
  useEffect(() => {
    const isPreviousSessionNotesEmpty = getPreviousSession(state)?.messages !== null  && getPreviousSession(state)?.notes === null
    console.log(isPreviousSessionNotesEmpty);

    if (isPreviousSessionNotesEmpty && !state.auroraIsThinking ) {
      console.log('going to get previous session notes');
      fetchGPTAnswer(createSessionNotesPrompt(state), state, dispatch)
        .then((notes) => {
          console.log('going to set the notes', notes);
          dispatch({
            type: ACTIONS.ADD_PREV_SESSION_NOTES,
            payload: { notes: notes }
          });
          dispatch({
            type: ACTIONS.SET_THINKING,
            payload: false
          });
        })
    }

  }, [state, dispatch])

  // // fill in previous message CM details if empty
  // useEffect(() => {
  //   const lastMessage = getLastMessage(getCurrentSession(state).messages);
  //   const isPreviousMessageCMEmpty = getLastMessage(getCurrentSession(state).messages)?.cmFlagged === null
  //   console.log('last message',lastMessage);
  //   console.log('previous message cm details empty',isPreviousMessageCMEmpty);
  //   console.log('aurora is thinking', state.auroraIsThinking);
  //   if (isPreviousMessageCMEmpty && !state.auroraIsThinking) {
  //     fetchContentModeration(lastMessage.message, state, dispatch)
  //     .then((data) => {
  //       dispatch({
  //         type: ACTIONS.ADD_MESSAGE_CM,
  //         payload: {
  //           cmFlagged: data.results[0].flagged,
  //           cmCategories: data.results[0].categories,
  //           cmCategoryScores: data.results[0].category_scores
  //         }
  //       });
  //       // dispatch({
  //       //   type: ACTIONS.SET_THINKING,
  //       //   payload: false
  //       // });
  //     })

  //   }

  // }, [state, dispatch]);


  // // first message
  useEffect(() => {
    const isTimetoTalk =  getCurrentSession(state).messages.length===0;

    console.log(getCurrentSession(state).messages);
    console.log(getCurrentSession(state).messages.length===0);
    console.log('time to talk', isTimetoTalk);
    console.log('aurora is thinking', state.auroraIsThinking);

    // known weird bug - on new session, fetchGPTAnswer always runs twice
    // needed to force a check to see if therapist already answered to not publish subsequent responses
    if (isTimetoTalk && !state.auroraIsThinking) {
      console.log('going to check API');
      if (isValidApiKey(state.apiKey))  {
          console.log('api check is good, going to fetch');
          fetchGPTAnswer(createNextMessagePrompt(state), state, dispatch)
            .then((message) => {
              if (getCurrentSession(state).messages.length===0) {
                console.log(getCurrentSession(state).messages.length===0);
                const nextMessage = buildMessage(
                  message,
                  false
                );
                // set message
                console.log('going to set the next message');
                dispatch({
                  type: ACTIONS.ADD_MESSAGE,
                  payload: { message: nextMessage }
                });
                dispatch({
                  type: ACTIONS.SET_THINKING,
                  payload: false
                });
              }
            })
        }
        else {
          console.log('api key check failed');
          setAPIKeyFromParms({state, dispatch})
      }
    }
  }, [state, dispatch]);

  // // therapist response
  useEffect(() => {

    const lastMessage = getLastMessage(getCurrentSession(state).messages);
    const isLastMessageFromUser = lastMessage?.isUser === true;

    if (isLastMessageFromUser && !state.auroraIsThinking) {
      console.log('going to check API');
      if (isValidApiKey(state.apiKey))  {
          console.log('api check is good, going to fetch');
          fetchContentModeration(lastMessage.message, state, dispatch)
            .then ((data) => {
              let highestCategory = null
              // if message is flagged = true,
              if (data.results[0].flagged) {
                // Get the category with the highest score
                highestCategory = Object.entries(data.results[0].category_scores).reduce((highest, [category, score]) => {
                  if (!highest || score > highest[1]) {
                      return [category, score];
                  }
                  return highest;
                }, null)[0];
              }
              console.log(highestCategory);
              fetchGPTAnswer(createNextMessagePrompt(state, highestCategory), state, dispatch)
                .then((message) => {
                  if (isLastMessageFromUser || getCurrentSession(state).messages.length===0) {
                    console.log(isLastMessageFromUser || getCurrentSession(state).messages.length===0);
                    const nextMessage = buildMessage(
                      message,
                      false
                    );
                    // set message
                    console.log('going to set the next message');
                    dispatch({
                      type: ACTIONS.ADD_MESSAGE,
                      payload: { message: nextMessage }
                    });
                  dispatch({
                      type: ACTIONS.SET_THINKING,
                      payload: false
                    })
                  }
                })
            })
        }
        else {
          console.log('api key check failed');
          setAPIKeyFromParms({state, dispatch})
      }
    }
  }, [state, dispatch]);

  return (
    <Container>
        <Menu state={state} dispatch={dispatch} />
        <Header />
        <Chat sessions={state.sessions} scrollViewRef={scrollViewRef} />
        <PopUp state={state} dispatch={dispatch} fetchGPTAnswer={fetchGPTAnswer}/>
        <Footer state={state} dispatch={dispatch} fetchContentModeration={fetchContentModeration} />
   </Container>
  );
}