import querystring from 'query-string';
import * as ACTIONS from './constants/actions';
const therapyStyleDefault = 'She uses a variety of techniques to give thoughtful answers to her client.'
const summarization_prompt = "\nAurora's session notes: \n"

export async function fetchGPTAnswer(prompt, state, dispatch) {
  dispatch({
    type: ACTIONS.SET_THINKING,
    payload: true
  })

  console.log('going to ask gpt');
  console.log(prompt);

  const requestOptions = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + String(state.apiKey)
    },
    body: JSON.stringify({
      'prompt': prompt,
      'temperature': 0.9,
      'max_tokens': 150,
      'top_p': 1,
      'frequency_penalty': 0,
      'presence_penalty': 0.6,
      'stop': ['AURORA:','CLIENT:',state.userName.toUpperCase()+':']
    })
  };

  try {
    const response = await fetch('https://api.openai.com/v1/engines/text-davinci-003/completions', requestOptions);
    const data = await response.json();
    const message = data.choices[0].text.trim();
    console.log(message);
    return  message ;
  } catch (err) {
    console.log(err);
    console.log(err.message);
    return "Sorry my servers are overloaded right now - can you try again in a little bit?";
  }
}

