import {
  ScrollView
} from 'react-native-web';

import Session from './Session';

export default function Chat({ sessions = [], scrollViewRef }) {
  return (
    <ScrollView
      ref={scrollViewRef}
      style={{
        paddingHorizontal: 20,
        paddingVertical: 10
      }}
      contentContainerStyle={{ flex: 1 }}
    >
      {sessions.map((it, index) => (
        <Session
          key={index.toString()}
          session={it}
        />
      ))}
    </ScrollView>
  );
}